import React from 'react';

import './App.css';
import About from './components/About';
import Achievements from './components/Achievements';
import Home from './components/Home';
import Projects from './components/Projects';
import Resume from './components/Resume';
import Skills from './components/Skills';
import logo from './img/logo/logo.ico';

import FB from './img/socials/facebook.png';
import GH from './img/socials/github.png';
import GM from './img/socials/gmail.png';
import IG from './img/socials/instagram.png';
import LN from './img/socials/linkedin.png';


export default class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            display: 'home',
            extendFB: false,
            extendIG: false,
            extendGM: false,
            extendLN: false,
            extendGH: false,
        }
    }

    navBtn = name => {
        if (this.state.display === name)
            return 'navBtnSelected';
        return 'navBtn';
    }

    render() {
        return (
            <div>
                <div className="optionBar">
                    <div className="optTopLeftSection">
                        <img className="icon" src={logo} alt="" />
                        <button className={this.navBtn('home')} onClick={() => this.setState({ display: 'home' })}>
                            Home
                        </button>
                    </div>
                    <div className="optTopRightSection">
                        <button className={this.navBtn('about')} onClick={() => this.setState({ display: 'about' })}>
                            About Me
                        </button>
                        <button className={this.navBtn('skills')} onClick={() => this.setState({ display: 'skills' })}>
                            Skills
                        </button>
                        <button className={this.navBtn('achievements')} onClick={() => this.setState({ display: 'achievements' })}>
                            Achievements
                        </button>
                        <button className={this.navBtn('projects')} onClick={() => this.setState({ display: 'projects' })}>
                            Projects
                        </button>
                        <button className={this.navBtn('resume')} onClick={() => this.setState({ display: 'resume' })}>
                            Resume
                        </button>
                    </div>
                </div>
                <div className='topBar' />
                <div className="content">
                    {this.state.display === 'home' && <Home />}
                    {this.state.display === 'about' && <About />}
                    {this.state.display === 'skills' && <Skills />}
                    {this.state.display === 'achievements' && <Achievements />}
                    {this.state.display === 'projects' && <Projects />}
                    {this.state.display === 'resume' && <Resume />}
                </div>
                <div className="optionBar">
                    <div className="optBottomLeftSection" style={{
                        background: 'rgb(55, 55, 55)',
                        background: 'linear-gradient(90deg, rgba(55, 55, 55, 1) 0%, rgba(12,12,12,1) 100%)',
                        height: '5ch',
                        marginRight: '10ch',
                    }} />
                    <div className="optBottomRightSection">
                        <button
                            className="contactBtn"
                            onMouseEnter={() => this.setState({ extendFB: true })}
                            onMouseLeave={() => this.setState({ extendFB: false })}
                        >
                            <div className="contactBtnContent">
                                <img className="contactIcon" src={FB} alt="" />
                                {this.state.extendFB && <p className="contactBtnText">Facebook</p>}
                            </div>
                        </button>

                        <button
                            className="contactBtn"
                            onMouseEnter={() => this.setState({ extendIG: true })}
                            onMouseLeave={() => this.setState({ extendIG: false })}
                        >
                            <div className="contactBtnContent">
                                <img className="contactIcon" src={IG} alt="" />
                                {this.state.extendIG && <p className="contactBtnText">Instagram</p>}
                            </div>
                        </button>

                        <button
                            className="contactBtn"
                            onMouseEnter={() => this.setState({ extendGM: true })}
                            onMouseLeave={() => this.setState({ extendGM: false })}
                        >
                            <div className="contactBtnContent">
                                <img className="contactIcon" src={GM} alt="" />
                                {this.state.extendGM && <p className="contactBtnText">Gmail</p>}
                            </div>
                        </button>

                        <button
                            className="contactBtn"
                            onMouseEnter={() => this.setState({ extendLN: true })}
                            onMouseLeave={() => this.setState({ extendLN: false })}
                        >
                            <div className="contactBtnContent">
                                <img className="contactIcon" src={LN} alt="" />
                                {this.state.extendLN && <p className="contactBtnText">LinkedIn</p>}
                            </div>
                        </button>

                        <button
                            className="contactBtn"
                            onMouseEnter={() => this.setState({ extendGH: true })}
                            onMouseLeave={() => this.setState({ extendGH: false })}
                        >
                            <div className="contactBtnContent">
                                <img className="contactIcon" src={GH} alt="" />
                                {this.state.extendGH && <p className="contactBtnText">Github</p>}
                            </div>
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}
